import React from "react"
import { Link } from "gatsby"
import Img from "gatsby-image"
import styled from 'styled-components'

export default function SingleProductPage({ data }){
	const product = data.wcProducts;
  const categories = product.categories;
  console.log(categories);
	return (
		<div>
      <div className="productleft">
  		  <Img fluid={product.images[0].localFile.childImageSharp.fluid}/>
      </div>
      <div className="productright">
    		<h1>{product.name}</h1>
        <span dangerouslySetInnerHTML={{ __html: product.price_html }} /> 
        <div dangerouslySetInnerHTML={{ __html: product.short_description }} /> 
        <Link to={`/product/${product.slug}`} > Add to Cart </Link>
        <div className="product_meta">
          <span className="sku_wrapper">SKU: <span className="sku">{product.sku}</span></span>
          <span className="posted_in">Category: 
            { categories.map(cat => (
            <Link key={cat.id} to={`/product-category/${cat.slug}`} >{cat.name}</Link>
            ))}
          </span>
        </div>
      </div>
		 
		</div>
	)
}

export const query = graphql`
  query($slug: String!) {
  wcProducts(slug: {eq: $slug}) {
    name
    slug
    on_sale
    sku
    price_html
    price
    short_description
    type
    attributes {
      slug
      name
      options
    }
    categories {
      id
      name
      slug
      wordpress_id
    }
    description
    images {
      localFile {
        childImageSharp {
            fluid {             
                ...GatsbyImageSharpFluid
           }
            
        }
      }
    }
  }
}

`;